import React, { Component } from "react";
import Link from "next/link";
import fetch from "isomorphic-unfetch";
import Error from "next/error";
//import PageWrapper from "../components/PageWrapper.js";
//import { Config } from "../config.js";
//import { Title } from "../style-config.js";

class ImageGrid extends Component {
    render() {
        console.log('posts', this.props.posts)
        const posts = this.props.posts.map((post, index) => {
            var background = post.acf.image.sizes?post.acf.image.sizes.large:'white'
            return (
                <div className="w-100 md:w-1/3 sm:w-1/2 cursor-pointer" key={index}>

                        <Link
                            as={`/post/${post.slug}`}
                            href={`/post?slug=${post.slug}&apiRoute=post`}
                        >
                        <div className="flex-1" style={{
                          background: 'url('+background+')',
                          backgroundSize: '100%',
                          backgroundRepeat: 'no-repeat',
                          backgroundPosition: 'right top',
                          minHeight: '300px'
                        }}>


                              <div className="absolute pin-bottom pin-left">
                                  <a>{post.title.rendered}</a>
                              </div>
                          </div>
                        </Link>

                </div>
            );
        });
        return (
            <div className="flex flex-wrap max-w-3xl">
                {posts}
            </div>
        );
    }
}

export default ImageGrid;
